@import "app/styles/mixins";

.Footer {
    background-color: var(--black3);
    padding: 50px 0;
    color: var(--white);
    position: relative;

    .infoText {
        font-size: 12px;
        line-height: 20px;
        opacity: 0.5;
        text-align: center;
        max-width: 800px;
        margin-bottom: 20px;
        @include md {
            font-size: 14px;
            margin-bottom: 30px;
        }
    }

    .socialIcons {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 10px;

        .icon {
            width: 50px;
            height: 50px;
            border-radius: 50%;
            background-color: var(--black);
            display: flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;
            transition: all ease 0.3s;
            color: var(--white);

            &:hover {
                box-shadow: 0 0 0.625em var(--pink);
                color: var(--pink);
            }
        }
    }
}
